<template>
  <div class="container">
    <h1>My Quote DB</h1>
    <div class="add-quote-section">
      <h3 for="add-quote">Add A Quote:</h3>
      <div class="add-quote">
        <input type="text" id="add-quote" v-model="quote" placeholder="Add A Quote">
        <input type="text" id="add-whoSaid" v-model="whoSaid" placeholder="Add A Who Said">
        <div v-on:click="createQuote" class="add-quote-btn">Add Quote!</div>
      </div>
    </div>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="quotes-section">
      <h1 class="quotes-title">Quotes</h1>
      <div class="quotes-container">
        <div
          class="quote"
          v-for="(quote, index) in quotes"
          v-bind:item="quote"
          v-bind:index="index"
          v-bind:key="quote._id"
          v-on:dblclick="deleteQuote(quote._id)"
        >
          <!-- {{ `${quote.createdAt.getDate()}/${quote.createdAt.getMonth()}/${quote.createdAt.getFullYear()}` }} -->
          <div class="text">"{{ quote.quote }}"</div>
          <div class="whoSaid">Spoken by: {{ quote.whoSaid }}</div>
          <!-- <button class="delete" v-on:click="deleteQuote(quote._id)">X</button> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QuoteService from "../QuoteService";

export default {
  name: "Quote",
  data() {
    return {
      quotes: [],
      error: "",
      quote: "",
      whoSaid: ""
    };
  },
  async created() {
    try {
      this.quotes = await QuoteService.getQuotes();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async createQuote() {
      await QuoteService.insertQuote(this.quote, this.whoSaid);
      this.quotes = await QuoteService.getQuotes();
    },
    async deleteQuote(id) {
      await QuoteService.deleteQuote(id);
      this.quotes = await QuoteService.getQuotes();
    }
  }
};
</script>

